.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItemA{
    flex:1;
    margin: 0px 20px;
    margin-top: 10px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    background-color: #faebd7;
    -webkit-box-shadow: 0px 0px 15px -10px #000000bf;
    box-shadow: 0px 0px 15px -10px #000000bf;
}

