.sideNavContainer{
    flex:1;
    /* height: calc(140vh - 70px); */
    position: -webkit-sticky;
    position: sticky;
    background-color: aliceblue;
    top: 50px;
}

.sideNavWrapper{
    padding: 20px;
    color: #555555;
}

.sideNavMenu{
    margin-bottom: 10px;
}

.sideNavTitle{
    font-size: 13px;
    color: #bbbaba;
}

.sideNavList{
    list-style:none;
    padding:  5px;
}

.sideNavListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 5px;
}

.sideNavListItem.active, .sideNavListItem:hover{
    background-color: #f0f0ff;
    color: #000000;
}

.sideNavIcon{
    margin-right: 5px;
    font-size: 20px;
    color: #1e90ff;
}