.userList{
    flex:4;
}

.userImg{
    width:40px;
    height: 40px;
    border-radius:50%;
    margin-right: 9px;
    object-fit: cover;
}

.user{
    display:flex;
    align-items:center;
}

.editUser{
    border:none;
    border-radius:5px;
    padding:5px 10px;
    background-color: #1e90ff;
    color: #ffffff;
    margin-right: 20px;
}

.deleteList{
    color:#ff0000;
    cursor:pointer;
}