.topNavContainer{
    width: 100%;
    height: 50px;
    background-color: #f0f8ff;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.topNavWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-weight: bold;
    font-size: 30px;
    color: #f0f8ff;
    cursor: pointer;
    background-color: #ff0000;
    border-radius:5px;
    font-family: Arial, Helvetica, sans-serif, monospace;
    margin-top: 3px;
    padding: 5px;
}

.topRight{
    display:flex;
    align-items:center;
}

.topNavIconContainer{
    position: relative;
    cursor: pointer;
    margin-right: 10px;
}

.topIconBadge{
    width:15px;
    height:15px;
    position:absolute;
    top: -5px;
    right: 5px;
    background-color: #ff0000;
    color: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topNavIcon{
    color: #1e90ff;
}

.adminImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}