.displayContainer{
    flex:2;
    margin-right: 15px;
    -webkit-box-shadow: 2px 4px 10px 1px #00000078;
    box-shadow: 2px 4px 10px 1px #c9c9c978;
    margin-top:20px;
    border-radius:12px;
}

.top{
    display:flex;
    align-items:center;
    justify-content: space-evenly;
    color:#808080;
}

.title{
    font-size:18px;
    font-weight:bold;
}

.bottom{
    padding:20px;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 14px;
}

.featuredChart{
    width: 100px;
    height: 100px;
}

.amount{
    font-size: 30px;
}

.description{
    font-weight: 300;
    font-size: 12px;
    color: #808080;
    text-align: center;
}

.summary{
    width:100%;
    display:flex;
    align-items:center;
    justify-content:space-between;
}

.item{
    text-align:center;
}

.displayItemTitle{
    font-size:15px;
    color:#808080;
}

.itemResult{
    display:flex;
    align-items:center;
    margin-top: 10px;
    font-size: 13px;
}

.positive{
    color:#008000;
}

.negative{
    color: #ff0000;
}