.profileTopContainer{
    flex:6;
    padding: 20px;
}

.profileTitleContainer{
    display:flex;
    align-items:items;
    justify-content: space-between;
    -webkit-box-shadow: 2px 4px 10px 1px #00000078;
    box-shadow: 2px 4px 10px 1px #c9c9c978;
    background-color: #1e90ff;
}

.profileTitle{
    color: #f0f8ff;
    margin-left: 10px;
    font-weight: 300;
}

.profileAddButton{
    width: 100px;
    border: none;
    padding: 5px;
    margin-right: 10px;
    background-color: #7fffd4;
    border-radius:5px;
    cursor:pointer;
}

.profileContainer{
    display:flex;
    margin-top:19px;
    -webkit-box-shadow: 2px 4px 10px 1px #00000078;
    box-shadow: 2px 4px 10px 1px #c9c9c978;
}

.profileShow{
    flex:1;
    padding: 19px;
}

.profileShowTop{
    display: flex;
    align-items: center;
}

.profileUpdate{
    flex:2;
    padding: 19px;
    margin-left: 19px;
}

.profileShowImg{
    height:100px;
    width:100px;
    border-radius: 50%;
    object-fit: cover;
}

.profileDetails{
    display:flex;
    flex-direction: column;
    margin-left:19px;
}

.myProfileName{
    font-weight: 300;
}

.myProfileTitle{
    font-weight:300;
}

.profileShowButton{
    margin-top: 10px;
}

.profileShowTitle{
    font-size: 15px; 
    font-weight:600;
    color: #afaaaa;
}

.profileShowInfo{
    display:flex;
    align-items:center;
    margin: 19px 0px;
    color: #444444;
}

.profileShowIcon{
    color: #1e90ff;
 }

.profileShowInfoTitle{
    margin-left: 12px;
}

.profileUpdateTitle{
    font-size: 22px;
    font-weight: 600;
}

.profileUpdateForm{
    display: flex;
    justify-content: space-between;
    margin-top: 19px;
}

.profileUpdateItem {
    display: flex;
    flex-direction: column;
}

.profileUpdateItem > label{
    margin-bottom: 5px;
    font-size: 17px;
}

.profileUpdateInput{
    width: 250px;
    height: 21px;
    padding: 5px;
    border: 1px solid #808080;
    border-radius: 5px;
}

.profileUpdateRight{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.profileUpdateUpload{
    display: flex;
    align-items: center;
}

.profileUpdateIcon{
    cursor: pointer;
}

.profileUpdateImg{
    width:80px;
    height:80px;
    border-radius:5px;
    object-fit:cover;
    margin-left:19px;
}

.profileUpdateButton{
    border: 5px;
    padding: 5px;
    cursor: pointer;
    font-weight: 600;
    background-color: dodgerblue;
    color: white;
}