.memberWidgetContainer{
    flex: 1;
    padding: 10px;
    margin-left: 20px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
 }

.memberWidgetTitle{
    font-size: 20px;
    font-weight: 600;
}

.widgetImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.memberWidgetList {
    margin: 0;
    padding: 0;
    list-style: none;
}

.memberWidgetListItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}

.memberWidgetUser{
    display: flex;
    flex-direction: column;
}

.memberWidgetUsername {
    font-weight: 600;
}

.memberWidgetUserTitle {
    font-weight: 300;
}

.memberWidgetButton {
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}

.memberWidgetIcon {
    font-size: 16px;
    margin-right: 5px;
} 