.orderContainer{
    flex: 2;
    padding: 20px;
    -webkit-box-shadow: 2px 4px 10px 1px #00000078;
    box-shadow: 2px 4px 10px 1px #c9c9c978;
}

.orderTitle{
    font-size: 20px;
    font-weight: 500;
    margin-right: 20px;  
}

.orderTable{
    width: 100%;
    border-spacing: 20px;
}

.orderUser{
    display: flex;
    align-items: center;
}

.orderTh{
    text-align: left;
}

.orderImg{
    width: 45px;
    height: 45px;
    border-radius: 5px;
    border: 1px solid #ff0000;
    object-fit: cover;
    margin-right: 10px;
}

.orderButton{
    padding: 5px 7px;
    border: none;
    border-radius: 5px;
}

.orderButton.Approved{
    background-color: #e5faf2;
    color: #3bb077;
}

.orderButton.Pending{
    background-color: #ffff6a;
    color: #99990a;
}

.orderButton.Cancel{
    background-color: #fac7cb;
    color: #d95087;
}