.createUserContainer{
    flex: 6;
    padding: 20px;
}

.newUserTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
    -webkit-box-shadow: 2px 4px 10px 1px #00000078;
    box-shadow: 2px 4px 10px 1px #c9c9c978;
    background-color: #1e90ff;
    color: #f0f8ff;
    padding: 5px;
}

.createUserForm{
    display: flex;
    flex-wrap: wrap;
}

.newUserItem{
    width: 400px;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-right: 20px;
}

.newUserItem > label{
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: #808080;
}

.newUserItem > input{
    height: 20px;
    padding: 10px;
    border: 1px soloid #808080;
    border-radius: 5px;
}

.newUserGender > input{
    margin-top: 15px;
}

.newUserGender > label{
    margin: 10px;
    font-size: 18px;
    color: #555555;
}

.newUserSelect{
    height: 40px;
    border-radius: 5px;
}

.newUserButton{
    width: 200px;
    border: none;
    border-radius: 10px;
    background-color: #1e90ff;
    color: #f0f8ff;
    padding: 7px 10px;
    font-weight: 600;
    margin-top: 30px;
    cursor: pointer;
}